html {
  font-size: 62.5%;
}

body {
  font-family: "Kanit", sans-serif;
  color: #ffff;
  background-color: #07041c;
  font-weight: 300;
  line-height: 1.6;
  box-sizing: border-box;
}

.container {
  width: 114rem;
  max-width: 100%;
  margin: 0 auto;
}

h1 {
  text-transform: uppercase;
}

header {
  background-color: #07041c;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 7.4rem;
}
.header__logo {
  flex-grow: 1;
  width: 6.1rem;
  margin-right: 1.92rem;
}
.header__nav {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-grow: 2;
}
.header__nav li {
  list-style: none;
  font-weight: 400;
  font-size: 1.6rem;
  margin-right: 5rem;
}
.header__nav li a {
  color: #ffff;
  text-decoration: none;
}
.header__nav li:first-child {
  margin-right: 3rem;
  padding: 1rem 2.5rem;
  font-weight: 700;
  border: 1px solid #fff;
  font-size: 1.5rem;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 5rem;
  line-height: 2.5rem;
  text-decoration: none;
  color: #ffff;
}
.header__nav li:last-child {
  margin-right: 0;
  margin-left: -1rem;
  padding: 1.2rem 2.75rem;
  position: relative;
  text-decoration: none;
  display: inline-block;
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: 700;
  background: linear-gradient(237.12deg, #ffcea8 0%, #c3a6fc 50.52%, #80fef7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.header__nav li:last-child::before {
  content: "";
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50px;
  padding: 1.7px;
  background: linear-gradient(237.12deg, #ffcea8 0%, #c3a6fc 50.52%, #80fef7 100%);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}

main {
  width: 100%;
  height: 101.6rem;
  margin: 0 auto;
  background: radial-gradient(65.51% 65.51% at 50% 19.09%, #221c3f 0%, #07041c 100%);
  padding-top: 0.1rem;
}

.showcase {
  margin-top: 5.1rem;
  background: linear-gradient(180deg, #14102b 0%, #282349 100%);
  height: 38.4rem;
  border-radius: 0.8rem;
  display: grid;
  grid-template-areas: "content svg" "content svg";
  grid-template-columns: repeat(2, 1fr);
}
.showcase__left {
  grid-area: content;
  width: 56.5rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 1.9rem 6rem;
}
.showcase__title {
  font-weight: 800;
  font-size: 4rem;
  line-height: 6rem;
}
.showcase__cta {
  position: relative;
  text-decoration: none;
  display: inline-block;
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: 700;
  background: linear-gradient(237.12deg, #ffcea8 0%, #c3a6fc 50.52%, #80fef7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  padding: 0.9rem 3.9rem;
  font-size: 1.6rem !important;
  margin-top: 0.7rem;
}
.showcase__cta::before {
  content: "";
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50px;
  padding: 1.7px;
  background: linear-gradient(237.12deg, #ffcea8 0%, #c3a6fc 50.52%, #80fef7 100%);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}
.showcase__content {
  margin-top: -1rem;
  font-weight: 400;
  font-size: 1.8rem;
  line-height: 2.8rem;
  letter-spacing: 0.5px;
}
.showcase__logos {
  margin-top: 3.6rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.showcase__logos a {
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 4rem;
  margin-right: 2.1rem;
  height: 2.5rem;
}
.showcase__logos a:nth-child(1) {
  width: 7rem;
}
.showcase__logos a:nth-child(1) img {
  width: 40px;
  transform: translate(42%, 50%);
}
.showcase__logos a:nth-child(2) {
  width: 11.4rem;
}
.showcase__logos a:nth-child(2) img {
  width: 10rem;
  transform: translate(9%, 32%);
}
.showcase__logos a:nth-child(3) {
  width: 6.8rem;
}
.showcase__logos a:nth-child(3) img {
  width: 5rem;
  transform: translate(18%, 22%);
}
.showcase__right {
  grid-area: svg;
}
.showcase__right img {
  margin-top: -3.2rem;
  margin-right: 1.7rem;
  width: 46rem;
}

.bonus {
  position: relative;
  margin-top: 1rem;
  display: grid;
  grid-template-areas: "svg content content" "svg content content";
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, minmax(0px, auto));
}
.bonus__left {
  grid-area: svg;
}
.bonus__left img {
  width: 40.9rem;
}
.bonus__left__title {
  font-size: 3.5rem;
  line-height: 40px;
}
.bonus__right {
  grid-area: content;
  padding: 4rem;
}
.bonus__right__img {
  grid-area: img;
  position: absolute;
  top: -6rem;
  right: -12rem;
}
.bonus__right__content {
  margin-left: 11.2rem;
  max-width: 38.2rem;
}
.bonus__right__content h2 {
  font-size: 3.5rem;
  font-weight: 800;
  line-height: 4rem;
}
.bonus__right__content span {
  font-weight: 400;
  font-size: 2.5rem;
  display: block;
}
.bonus__right__content__coin {
  margin-top: 1.2rem;
}
.bonus__right__content__cta {
  position: relative;
  text-decoration: none;
  display: inline-block;
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: 700;
  background: linear-gradient(237.12deg, #ffcea8 0%, #c3a6fc 50.52%, #80fef7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-top: -1rem;
  padding: 0.9rem 4.5rem;
}
.bonus__right__content__cta::before {
  content: "";
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50px;
  padding: 1.7px;
  background: linear-gradient(237.12deg, #ffcea8 0%, #c3a6fc 50.52%, #80fef7 100%);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}

.list {
  margin-top: 1.1rem;
}
.list__title {
  text-align: center;
  font-weight: 800;
  font-size: 3.5rem;
  line-height: 5.2rem;
  text-transform: uppercase;
}
.list__nav {
  display: flex;
  align-items: center;
  justify-content: center;
}
.list__nav li {
  list-style: none;
  margin-right: 1rem;
}
.list__nav li > .default {
  padding: 1.25rem 4.5rem;
  font-weight: 700;
  border: 1px solid #fff;
  font-size: 1.5rem;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 5rem;
  line-height: 2.5rem;
  text-decoration: none;
  color: #ffff;
}
.list__nav li > .active {
  padding: 1.25rem 4.5rem;
  position: relative;
  text-decoration: none;
  display: inline-block;
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: 700;
  background: linear-gradient(237.12deg, #ffcea8 0%, #c3a6fc 50.52%, #80fef7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.list__nav li > .active::before {
  content: "";
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50px;
  padding: 1.7px;
  background: linear-gradient(237.12deg, #ffcea8 0%, #c3a6fc 50.52%, #80fef7 100%);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}

.cardlist__card {
  box-sizing: border-box;
  margin-top: 2.5rem;
  width: 100%;
  height: 13.2rem;
  border: 0.8rem;
  background-color: #241f43;
  border-radius: 0.8rem;
  padding: 4.7rem 2.35rem 5.3rem 6.5rem;
  display: flex;
  align-items: center;
}
.cardlist__card__num {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #07041c;
  font-weight: 500;
  font-size: 1.7rem;
  width: 4rem;
  height: 4rem;
  background: linear-gradient(237.12deg, #ffcea8 0%, #c3a6fc 50.52%, #80fef7 100%);
  border-radius: 14rem;
}
.cardlist__card:first-child img {
  margin-left: 1.1rem;
}
.cardlist__card:first-child .cardlist__card__logo {
  margin-right: 3.7rem;
}
.cardlist__card__logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-left: 4.45rem;
  max-width: 12rem;
}
.cardlist__card__logo__img {
  width: 100%;
}
.cardlist__card__logo span {
  display: flex;
  align-items: center;
  font-size: 1.4rem;
  font-weight: 500;
  margin-top: 1rem;
  width: max-content;
}
.cardlist__card__logo span img {
  margin-left: 1rem;
  width: 1.8rem;
}
.cardlist__card__content {
  margin-left: 4.9rem;
  display: flex;
  flex-direction: column;
  max-width: 20rem;
}
.cardlist__card__content li {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  font-weight: 500;
  font-size: 1.6rem;
}
.cardlist__card__content li img {
  margin-right: 0.9rem;
}
.cardlist__card__star {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-left: 7.4rem;
  margin-top: -1rem;
}
.cardlist__card__star p {
  font-weight: 500;
  font-size: 1.7rem;
}
.cardlist__card__rating {
  margin-top: -1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  flex-direction: row-reverse;
}
.cardlist__card__rating-0 {
  filter: grayscale(100%);
}
.cardlist__card__rating > input {
  display: none;
}
.cardlist__card__rating > label {
  cursor: pointer;
  width: 1.8rem;
  height: 1.8rem;
  margin-top: auto;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='126.729' height='126.73'%3e%3cpath fill='%23e3e3e3' d='M121.215 44.212l-34.899-3.3c-2.2-.2-4.101-1.6-5-3.7l-12.5-30.3c-2-5-9.101-5-11.101 0l-12.4 30.3c-.8 2.1-2.8 3.5-5 3.7l-34.9 3.3c-5.2.5-7.3 7-3.4 10.5l26.3 23.1c1.7 1.5 2.4 3.7 1.9 5.9l-7.9 32.399c-1.2 5.101 4.3 9.3 8.9 6.601l29.1-17.101c1.9-1.1 4.2-1.1 6.1 0l29.101 17.101c4.6 2.699 10.1-1.4 8.899-6.601l-7.8-32.399c-.5-2.2.2-4.4 1.9-5.9l26.3-23.1c3.8-3.5 1.6-10-3.6-10.5z'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 76%;
  transition: 0.3s;
}
.cardlist__card__rating > input:checked ~ label, .cardlist__card__rating > input:checked ~ label ~ label {
  background-image: url(../../img/star.svg);
}
.cardlist__card__rating > input:not(:checked) ~ label:hover, .cardlist__card__rating > input:not(:checked) ~ label:hover ~ label {
  background-image: url(../../img/star.svg);
}
.cardlist__card a {
  position: relative;
  text-decoration: none;
  display: inline-block;
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: 700;
  background: linear-gradient(237.12deg, #ffcea8 0%, #c3a6fc 50.52%, #80fef7 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-left: 6.1rem;
  font-size: 1.7rem;
  padding: 0.9rem 5.2rem;
}
.cardlist__card a::before {
  content: "";
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50px;
  padding: 1.7px;
  background: linear-gradient(237.12deg, #ffcea8 0%, #c3a6fc 50.52%, #80fef7 100%);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}
.cardlist__card__deposit {
  margin-left: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.cardlist__card__deposit span {
  color: #58607d;
  font-size: 1.7rem;
  font-weight: 500;
}
.cardlist__card__deposit b {
  font-size: 2.7rem;
  font-weight: 700;
}